	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Video Studio","date":"07 Apr 2002","slug":"/2002-04-07-video-studio/","tags":"Y2002"},"html":"<p>We finally got our video camera back into action, after 1) getting it repaired (on warranty) after (I suspect) Christopher jammed the cassette mechanism, and 2) finding that a head cleaning cassette solved our playback quality problem. And I think I finally found a usable video editing software, <a href=\"http://www.ulead.com/vs/runme.htm\">Ulead VideoStudio 6</a>. The demo has been very nice, much improved from version 4 that came with my FireWire card, and I think I'll end up buying it. So maybe we can finally start sending video CDs to Grandma and Grandpa, like we've been promising for months. </p>\n<p>(Yes, I know I could render mpegs and burn video CDs with Linux, but I don't want to take the time to mess with that.)</p>"},"next":{"fields":{"title":"Sunny but Cold","date":"06 Apr 2002","slug":"/2002-04-06-sunny-but-cold/"}},"previous":{"fields":{"title":"Ants and Toilet Paper","date":"08 Apr 2002","slug":"/2002-04-08-ants-and-toilet-paper/"}}},{"node":{"fields":{"title":"Sunny but Cold","date":"06 Apr 2002","slug":"/2002-04-06-sunny-but-cold/","tags":"Y2002"},"html":"<p><img alt=\"fphpatio.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/04/fphpatio.jpg\" width=\"350\" height=\"263\" border=\"0\" /></p>\n<p><img alt=\"crhpipe2.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/04/crhpipe2.jpg\" width=\"350\" height=\"263\" border=\"0\" /></p>"},"next":{"fields":{"title":"Off To The Sidebar","date":"05 Apr 2002","slug":"/2002-04-05-off-to-the-sidebar/"}},"previous":{"fields":{"title":"Video Studio","date":"07 Apr 2002","slug":"/2002-04-07-video-studio/"}}},{"node":{"fields":{"title":"Off To The Sidebar","date":"05 Apr 2002","slug":"/2002-04-05-off-to-the-sidebar/","tags":"Y2002"},"html":"<p>I worked on my sidebar this morning. My link list is back up. I should put it in a database or something.</p>"},"next":{"fields":{"title":"FC Bundesregierung","date":"05 Apr 2002","slug":"/2002-04-05-fc-bundesregierung/"}},"previous":{"fields":{"title":"Sunny but Cold","date":"06 Apr 2002","slug":"/2002-04-06-sunny-but-cold/"}}}],"pathPrefix":"","first":false,"last":false,"index":907,"pageCount":1101}}

